<template>
    <div class="AudiobookViewer" :style="borderTop">
        <div class="pt-5" :class="{ 'centerOnDektop h-100': centerOnDektop }">
            <div ref="audiobookPoster">
                <AudiobookPoster :currentTrack="currentTrack" />
            </div>
            <div ref="audiobookPlayer" class="py-4 playerContainer" :class="{ shadow: playerIsSticky }">
                <AudiobookPlayer
                    :track="track"
                    :index="track.index"
                    :totalIndex="totalIndex"
                    :playerIsSticky="playerIsSticky"
                    :isLatestPreviewTrack="isLatestPreviewTrack"
                    @playtrack="play"
                    @pausetrack="pause"
                    @rewindtrack="rewind"
                    @forwardtrack="forward"
                    @nexttrack="next"
                    @backtrack="back"
                    @setseektrack="setSeek"
                    @pauseseekposition="pauseSeekPosition"
                />
            </div>
            <div v-if="onlyMobileVisible">
                <portal-target name="playlist" />
            </div>
            <portal to="playlist">
                <AudiobookPlaylist :playlist="audiobook.playlist" :currentTrackIndex="track.index" @playtrack="play" />
            </portal>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex';
import { Howl, Howler } from 'howler';
import AudiobookPlayer from './AudiobookPlayer.vue';
import AudiobookPlaylist from './AudiobookPlaylist.vue';
import AudiobookPoster from './AudiobookPoster.vue';
import secondToMMSS from '_shared_/js/utils/secondToMMSS.js';

export default {
    components: {
        AudiobookPlayer,
        AudiobookPlaylist,
        AudiobookPoster,
    },
    data() {
        return {
            track: {
                index: 0,
                audio: null,
                seek: 0,
                playseek: 0,
                durationInSecondsPreview: 0,
            },
            seekPositionPause: false,
            playerIsSticky: false,
            lastLocalization: {
                seekPosition: 0,
                chapterPosition: 0,
            },
        };
    },
    computed: {
        ...mapState([
            'audiobook',
            'ui',
            'device',
            'title',
            'publication',
            'issue',
            'environment',
            'lastLocation',
            'isPlayBtn',
            'isLoadingAudio',
        ]),
        ...mapGetters(['onlyMobileVisible', 'onlyDesktopVisible', 'isBuyableIssue', 'isPreviewMode']),
        currentTrack() {
            return this.audiobook.playlist[this.track.index];
        },
        totalIndex() {
            return this.audiobook.playlist.length;
        },
        borderTop() {
            return `padding-top: ${this.ui.headerbar.offsetHeight}px`;
        },
        isPlaying() {
            if (this.track.audio === null) {
                return false;
            } else {
                return this.track.audio.playing();
            }
        },
        centerOnDektop() {
            // It's because in some devices, crop the cover
            return this.device.width > 720 && this.device.height > 520;
        },
        getAuthors() {
            if (this.issue.tags.hasOwnProperty('author')) {
                return this.issue.tags.author.length > 0 ? this.issue.tags.author.join(', ') + '. ' : '';
            }
            return '';
        },
        isLatestPreviewTrack() {
            if (!this.isPreviewMode) {
                return false;
            }

            return this.currentTrack.durationInSecondsPreview !== this.currentTrack.durationInSeconds;
        },
    },
    created() {
        this.initAudiobook();
        this.mustBeOpenPlaylist();
        document.addEventListener('keydown', this.handleKeyPressed, false);
    },
    mounted() {
        this.isSticky();
        this.sendLastLocation();
    },
    methods: {
        // see this mutation and cleanup
        ...mapMutations([
            'setLoadingState',
            'setLoadingAudioState',
            'setIsPlayBtn',
            'toggleUi',
            'openAudiobookPlaylist',
            'closeAudiobookPlaylist',
            'loadingAudiobookPlaylist',
            'togglePaymentLinks',
        ]),
        ...mapActions(['trackNavigation', 'setLastLocation']),
        initAudiobook() {
            this.loadLastLocation();
            this.loadingAudio(true);
            this.setLoadingState({ isLoading: false });
        },
        loadingAudio(firstLoad = false) {
            this.track.audio = new Howl({
                src: [this.currentTrack.file],
                format: ['mp3'],
                html5: true,
                autoplay: false,
                rate: this.audiobook.track.speed,
                preload: true,
                onend: this.next,
                onload: () => {
                    this.setMediaSessionData();
                    if (firstLoad) {
                        this.setSeek(this.track.seek);
                    }
                },
            });
            this.track.durationInSecondsPreview = this.currentTrack.durationInSecondsPreview;
        },
        play(index) {
            this.setLoadingAudioState({ isLoadingAudio: true });
            // when send index, we loading a new chapter and play
            if (typeof index === 'number') {
                this.stop();
                this.track.index = index;
                this.loadingAudio();
                this.sendLastLocation(true);
            }
            if (this.track.audio.state() === 'loaded') {
                this.track.audio.play();
            } else {
                this.track.audio.once('load', () => {
                    this.track.audio.play();
                });
            }
            this.track.audio.off('play');
            this.track.audio.once('play', () => {
                this.setIsPlayBtn({
                    isPlayBtn: false,
                });
                this.setLoadingAudioState({ isLoadingAudio: false });
            });
            /*
             * Show payments link when an user go to
             * next track in this is not disponible because is a preview and
             * the issue need is purchable
             */
            if (this.currentTrack.file === null) {
                this.reachedPreview();
            }
        },
        pause() {
            this.track.audio.pause();
            this.track.audio.off('pause');
            this.track.audio.once('pause', () => {
                this.setIsPlayBtn({ isPlayBtn: true });
                this.setLoadingAudioState({ isLoadingAudio: false });
            });
            this.sendLastLocation(true);
        },
        stop() {
            this.track.audio.stop();
        },
        rewind() {
            const seconds = this.track.seek >= 30 ? this.track.seek - 30 : 0;
            this.setSeek(seconds);
        },
        forward() {
            const duration = this.track.audio.duration();
            const seconds = this.track.seek + 30 >= duration ? duration : this.track.seek + 30;
            this.setSeek(seconds);
        },
        setSeek(seconds) {
            if (this.isPreviewMode && this.track.durationInSecondsPreview < seconds) {
                this.reachedPreview();
                return;
            }

            if (!this.isPlayBtn) {
                this.track.audio.pause();
                this.setLoadingAudioState({ isLoadingAudio: true });
            }

            this.track.audio.seek(seconds);
            this.seekPositionPause = false;
            if (this.isPlayBtn) {
                return;
            }

            if (!this.playseek) {
                this.track.audio.play();
                this.playseek = 1;
            }
            this.track.audio.once('play', () => {
                this.playseek = 0;
                this.setIsPlayBtn({ isPlayBtn: false });
                this.setLoadingAudioState({ isLoadingAudio: false });
            });
        },
        next() {
            this.stop();
            if (this.track.index === this.totalIndex) return;

            if (this.track.index < this.totalIndex) {
                this.track.index++;
            } else {
                this.track.index = this.totalIndex;
            }
            this.loadingAudio();
            this.sendLastLocation(true);
            this.play();
        },
        back() {
            this.pause();
            if (this.track.seek > 5) {
                this.setSeek(0);
                return this.play();
            }

            if (this.track.index !== 0) {
                this.track.index--;
            } else {
                this.track.index = 0;
            }
            this.loadingAudio();
            this.sendLastLocation(true);
            this.play();
        },
        mustBeOpenPlaylist() {
            if (this.device.width >= 1024) {
                this.openAudiobookPlaylist();
            }
        },
        handleKeyPressed(e) {
            if (e.keyCode === 32) {
                //SpaceKey
                e.preventDefault();
                this.isPlaying ? this.pause() : this.play();
                this.trackNavigation({
                    action: this.isPlaying ? 'pause' : 'play',
                    trigger: 'keyboard',
                    source: 'audiobook:player',
                });
            }

            if (e.keyCode === 39) {
                //ArrowLeft
                e.preventDefault();
                this.forward();
                this.trackNavigation({
                    action: 'forward',
                    trigger: 'keyboard',
                    source: 'audiobook:player',
                });
            }

            if (e.keyCode === 37) {
                //ArrowRight
                e.preventDefault();
                this.rewind();
                this.trackNavigation({
                    action: 'rewind',
                    trigger: 'keyboard',
                    source: 'audiobook:player',
                });
            }

            if (e.keyCode === 38) {
                //ArrowUp
                e.preventDefault();
                this.back();
                this.trackNavigation({
                    action: 'back',
                    trigger: 'keyboard',
                    source: 'audiobook:player',
                });
            }

            if (e.keyCode === 40) {
                //ArrowDown
                e.preventDefault();
                this.next();
                this.trackNavigation({
                    action: 'next',
                    trigger: 'keyboard',
                    source: 'audiobook:player',
                });
            }
        },
        pauseSeekPosition() {
            this.seekPositionPause = true;
        },
        setMediaSessionData() {
            if ('mediaSession' in navigator) {
                /**
                 * For more details: https://developers.google.com/web/updates/2017/02/media-session
                 * We use Media Session for catch the behavior of media keys
                 * and manage the audio using media notifications.
                 * The status is Draft https://developer.mozilla.org/en-US/docs/Web/API/Media_Session_API
                 * @todo Make it work in the most browsers.
                 */
                navigator.mediaSession.metadata = new MediaMetadata({
                    title: `${this.currentTrack.chapter} - ${this.currentTrack.title}`,
                    artist: this.getAuthors,
                    album: this.title,
                    artwork: [
                        {
                            src: this.publication.cover,
                        },
                    ],
                });
                navigator.mediaSession.setActionHandler('play', () => {
                    this.isPlaying ? this.pause() : this.play();
                    this.trackNavigation({
                        action: 'play',
                        trigger: 'mediaSession',
                        source: 'audiobook:player',
                    });
                });
                navigator.mediaSession.setActionHandler('pause', () => {
                    this.pause();
                    this.trackNavigation({
                        action: 'pause',
                        trigger: 'mediaSession',
                        source: 'audiobook:player',
                    });
                });
                /**
                 * We deactivate these function to playback control, because is doesn't woeking correctly when the tab is inactive.
                 * Happen when the phone is bloqued or was change a tab.
                 * Causes: When is used with the phone bloqued or the tab inactive, the actions don't dispatch fine
                 * but when unblock de phone screen, the actions chain execute causing overlapping audio or loading in the icon
                 * of play. The problem date: 08/16/2019
                 */

                // navigator.mediaSession.setActionHandler('seekbackward', () => {
                //     this.rewind();
                //     this.trackNavigation({
                //         action: 'rewind',
                //         trigger: 'mediaSession',
                //         source: 'audiobook:player',
                //     })
                // });
                // navigator.mediaSession.setActionHandler('seekforward', () => {
                //     this.forward();
                //     this.trackNavigation({
                //         action: 'forward',
                //         trigger: 'mediaSession',
                //         source: 'audiobook:player',
                //     })
                // });
                // navigator.mediaSession.setActionHandler('previoustrack', () => {
                //     this.back();
                //     this.trackNavigation({
                //         action: 'back',
                //         trigger: 'mediaSession',
                //         source: 'audiobook:player',
                //     })
                // });
                // navigator.mediaSession.setActionHandler('nexttrack', () => {
                //     this.next()
                //     this.trackNavigation({
                //         action: 'next',
                //         trigger: 'mediaSession',
                //         source: 'audiobook:player',
                //     })
                // });
            }
        },
        /**
         * We observe when audiobook is non visible to apply a shadow to sticky player.
         * For this we are using https://developer.mozilla.org/es/docs/Web/API/Intersection_Observer_API.
         */
        isSticky() {
            const observer = new IntersectionObserver(
                (entries) => {
                    this.playerIsSticky = entries[0].intersectionRatio === 0;
                },
                { threshold: [0, 1] }
            );
            observer.observe(this.$refs.audiobookPoster);
        },
        sendLastLocation(sendNow = false) {
            if (this.isPlaying || sendNow) {
                this.setLastLocation({
                    currentSeek: this.track.seek,
                    currentTrack: this.track.index,
                });
            }

            /**
             * @todo We can make it more frequent later.
             * Use a if because we use the same method for send the location when user change a chapter and avoid
             * init a new setTimeout every time to user change the chapter overlaping timeout and 🔥
             */
            if (!sendNow) {
                setTimeout(() => {
                    this.sendLastLocation();
                }, 60000);
            }
        },
        loadLastLocation() {
            this.track.index = this.lastLocation.audio.currentTrack;
            this.track.seek = this.lastLocation.audio.currentSeek;
        },
        reachedPreview() {
            this.track.audio.pause();
            if (this.isBuyableIssue) {
                this.togglePaymentLinks();
            }
        },
    },
    watch: {
        'audiobook.track.speed': function (speed) {
            this.track.audio.rate(speed);
        },
        isPlaying: function (playing) {
            if (this.currentTrack.file == null) {
                this.track.seek = 0;
                this.stop();
                return;
            }
            // It's necesary becuse the seek usually muast be and object.
            this.track.seek = typeof this.track.audio.seek() === 'number' ? this.track.audio.seek() : this.track.seek;
            let updateSeek;
            if (playing) {
                updateSeek = setInterval(() => {
                    // We not send the actual seek when the user change using the seekbar.
                    this.track.seek = this.seekPositionPause ? this.track.seek : this.track.audio.seek();
                }, 250);
            } else {
                clearInterval(updateSeek);
            }
        },
        'device.width': function (width) {
            if (width >= 1024) {
                this.openAudiobookPlaylist();
            } else {
                this.closeAudiobookPlaylist();
            }
        },
    },
};
</script>
<style lang="scss" scoped>
.AudiobookViewer {
    height: 100vh;
    overflow-y: auto;
    padding-bottom: 2rem;
    // fix for safari iOs
    -webkit-overflow-scrolling: touch;

    .centerOnDektop {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .playerContainer {
        position: sticky;
        top: 0;
        background: var(--theme-background-reader);
        z-index: $z1;
    }

    @media screen and (min-width: 571px) {
        display: flex;
        align-items: center;
        justify-content: center;
        width: calc(100% - 70px);
    }
    @media screen and (min-width: 1024px) {
        width: calc(100% - 500px);
    }
}
</style>
