<template>
    <div class="NarratorSpeedSelector d-flex align-items-center" v-click-outside="closeNarratorSpeed">
        <button
            type="button"
            class="d-flex justify-content-between align-items-center"
            data-cy="toggleShowSpeed"
            @click="showSpeed = !showSpeed"
        >
            <span class="w-100 text-center">{{ audiobook.track.speed }}&times;</span>
        </button>
        <transition name="slide-fade">
            <ul class="p-0 mt-1 shadow" v-show="showSpeed">
                <li class="p-3 listTitle text-truncate">
                    {{ localizations[lang].narratorSpeed }}
                </li>
                <li
                    v-for="(speed, index) in speeds"
                    :key="index"
                    class="px-3 py-2 text-truncate"
                    :class="{ isSelected: speed === audiobook.track.speed }"
                    @click="trackSelectSpeed(speed)"
                    :data-cy="`speedInput-${speed}`"
                >
                    {{ speed }}&times;
                    <span class="ml-1" v-if="speed === 1">(normal)</span>
                </li>
            </ul>
        </transition>
    </div>
</template>
<script>
import { mapState, mapActions, mapMutations, mapGetters } from 'vuex';
export default {
    data() {
        return {
            showSpeed: false,
            speeds: [0.5, 0.8, 1.0, 1.2, 1.5, 2.0],
        };
    },
    computed: {
        ...mapState(['ui', 'uiHidden', 'lang', 'localizations', 'audiobook']),
        ...mapGetters(['onlyDesktopVisible']),
    },
    methods: {
        ...mapMutations(['zoomByType', 'changeAudiobookSpeed']),
        ...mapActions(['trackEvent']),
        trackSelectSpeed(speed) {
            this.trackEvent({
                action: 'narratorSpeed',
                trigger: 'click',
                source: 'audiobook:player',
            });
            this.changeAudiobookSpeed({ speed: speed });
            this.showSpeed = false;
        },
        closeNarratorSpeed() {
            this.showSpeed = false;
        },
    },
};
</script>
