            &:hover {
                background: rgba(#707070, 0.16); // This color work in both color mode
            }
            &:active,
            &:focus {
                background: rgba(#707070, 0.26); // This color work in both color mode
            }
        }
        li:first-child {
            font-size: 0.875rem;
