<template>
    <div class="AudiobookPoster w-100 px-3 d-flex align-items-center" :class="{ 'flex-column': setLikeColumn }">
        <div>
            <div class="image-container mb-3">
                <img class="shadow" :src="publication.cover" :alt="title" />
            </div>
        </div>
        <div class="w-100">
            <h4 class="my-3 px-2 text-center">{{ currentTrack.chapter }}: {{ currentTrack.title }}</h4>
            <h5 class="my-3 px-2 text-center" v-if="narrators">
                {{ localizations[lang].narratedBy }}: {{ narrators }}
            </h5>
            <portal-target class="col-12 mb-2" name="timing-bar" />
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
    props: {
        currentTrack: Object,
    },
    computed: {
        ...mapState(['audiobook', 'publication', 'title', 'device', 'localizations', 'lang', 'issue']),
        setLikeColumn() {
            return this.device.height > 520;
        },
        narrators() {
            return this.issue.tags.hasOwnProperty('narrator') && this.issue.tags.narrator.length > 0
                ? this.issue.tags.narrator.join(', ')
                : null;
        },
    },
};
</script>
