<template>
    <div class="AudiobookPlayer">
        <div class="container-fluid w-100">
            <div class="row align-items-center justify-content-center">
                <!-- Moved `timing-bar` and `track audio duration` inside to portal because this elements
                is rendered in another component using a portal. With this, avoided use the store and can mantain
                the conection form child to parent -->

                <portal to="timing-bar">
                    <div class="w-100">
                        <input
                            type="range"
                            @change="sendSeekPosition"
                            @mousedown="pauseSeekPosition"
                            @touchstart="pauseSeekPosition"
                            v-model="positionSeek"
                            min="0"
                            :max="track.audio.duration()"
                            step="1"
                            data-cy="seekRangeInput"
                        />
                    </div>

                    <div class="col-12 text-center mb-5 timing d-flex align-items-center justify-content-center">
                        <span class="font-monospace">
                            {{ positionSeek | secondToMMSS }} /
                            <span v-if="!isPreviewMode" data-cy="playerTrackDuration">{{
                                track.audio.duration() | secondToMMSS
                            }}</span>
                        </span>
                        <span v-if="isPreviewMode && track.durationInSecondsPreview" class="badge badge-inline ml-2">
                            {{ localizations[lang].preview }}
                            <b class="font-monospace ml-2" data-cy="playerPreviewTrackDuration">{{
                                track.durationInSecondsPreview | secondToMMSS
                            }}</b>
                        </span>
                    </div>
                </portal>

                <div class="col-12">
                    <div class="row align-items-center justify-content-center justify-content-center">
                        <div
                            class="col-auto d-flex align-items-center"
                            :class="{
                                'mb-2': !showPlayerInline,
                                'px-0': xsDevices,
                                'px-1': onlyMobileVisible && !xsDevices,
                            }"
                        >
                            <NarratorSpeedSelector
                                class="px-2"
                                :class="{ 'mx-2': !showPlayerInline, isSticky: playerIsSticky }"
                            />
                            <span
                                style="min-width: 110px"
                                :class="{ 'd-none': showPlayerInline, small: onlyMobileVisible }"
                                >{{ narrationSpeedActionText }}</span
                            >
                        </div>
                        <div
                            class="d-flex align-items-center justify-content-center px-0"
                            :class="{ 'order-3 col-12 my-4': !showPlayerInline }"
                        >
                            <button
                                class="flatButton rewind mx-2"
                                data-action="rewind"
                                @click="rewind"
                                v-tooltip="localizations[lang].tooltips.rewind"
                                data-cy="rewindInput"
                            >
                                <svg-icon
                                    icon="baseline-replay_30-24px"
                                    :width="buttonSize.rewindAndForward"
                                    :height="buttonSize.rewindAndForward"
                                ></svg-icon>
                            </button>
                            <button
                                v-if="!xsDevices"
                                class="flatButton back mx-2"
                                data-action="back"
                                @click="back"
                                :disabled="disableBack"
                                v-tooltip="localizations[lang].tooltips.prev"
                                data-cy="backInput"
                            >
                                <svg-icon
                                    icon="baseline-skip_previous-24px"
                                    :width="buttonSize.nextAndBack"
                                    :height="buttonSize.nextAndBack"
                                ></svg-icon>
                            </button>
                            <button
                                @click="playOrPauseAudio"
                                class="flatButton play mx-2"
                                data-action="playOrPause"
                                :disabled="isLoading"
                                data-cy="playOrPauseInput"
                            >
                                <LoadingSpinner
                                    v-if="isLoading"
                                    :width="buttonSize.loading"
                                    :height="buttonSize.loading"
                                    :notCentered="true"
                                    data-cy="trackIsLoading"
                                />
                                <svg-icon
                                    v-else
                                    :icon="
                                        isPlayBtn
                                            ? 'baseline-play_circle_filled-24px'
                                            : 'baseline-pause_circle_filled-24px'
                                    "
                                    :width="buttonSize.play"
                                    :height="buttonSize.play"
                                ></svg-icon>
                            </button>
                            <button
                                v-if="!xsDevices"
                                class="flatButton mx-2"
                                data-action="next"
                                @click="next"
                                :disabled="disableNext"
                                v-tooltip="localizations[lang].tooltips.next"
                                data-cy="nextInput"
                            >
                                <svg-icon
                                    icon="baseline-skip_next-24px"
                                    :width="buttonSize.nextAndBack"
                                    :height="buttonSize.nextAndBack"
                                ></svg-icon>
                            </button>
                            <button
                                class="flatButton forward mx-2"
                                data-action="forward"
                                @click="forward"
                                v-tooltip="localizations[lang].tooltips.forward"
                                data-cy="forwardInput"
                            >
                                <svg-icon
                                    icon="baseline-forward_30-24px"
                                    :width="buttonSize.rewindAndForward"
                                    :height="buttonSize.rewindAndForward"
                                ></svg-icon>
                            </button>
                        </div>
                        <div
                            class="col-auto d-flex align-items-center justify-content-center"
                            :class="{
                                'mb-2': !showPlayerInline,
                                'px-0': xsDevices,
                                'px-1': onlyMobileVisible && !xsDevices,
                            }"
                        >
                            <ComponentFavoriteButton
                                class="mx-2"
                                :width="buttonSize.favorite"
                                :height="buttonSize.favorite"
                            />
                            <span :class="{ 'd-none': showPlayerInline, small: onlyMobileVisible }">{{
                                favoriteActionText
                            }}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex';
import ComponentFavoriteButton from '_shared_/UI/Components/ComponentFavoriteButton.vue';
import NarratorSpeedSelector from '_shared_/UI/Audiobooks/NarratorSpeedSelector.vue';
import LoadingSpinner from '_shared_/UI/Utilities/LoadingSpinner.vue';
import secondToMMSS from '_shared_/js/utils/secondToMMSS.js';

export default {
    components: {
        ComponentFavoriteButton,
        NarratorSpeedSelector,
        LoadingSpinner,
    },
    props: {
        track: Object,
        totalIndex: Number,
        index: Number,
        playerIsSticky: Boolean,
        isLatestPreviewTrack: Boolean,
    },
    mounted() {
        this.positionSeek = this.track.seek;
    },
    data() {
        return {
            positionSeek: 0,
            settingSeek: false,
        };
    },
    computed: {
        ...mapState(['audiobook', 'device', 'localizations', 'lang', 'isFavorite', 'isPlayBtn', 'isLoadingAudio']),
        ...mapGetters(['onlyMobileVisible', 'isPreviewMode', 'isBuyableIssue']),
        disableBack() {
            return this.index === 0;
        },
        disableNext() {
            return this.index === this.totalIndex - 1;
        },
        isPlaying() {
            return this.track.audio.playing();
        },
        isLoading() {
            return this.track.audio.state() === 'loading' || this.isLoadingAudio;
        },
        buttonSize() {
            let isDesktop = this.device.width >= 980;
            return {
                play: isDesktop ? `${85 * 1.4}px` : '85px',
                nextAndBack: isDesktop ? `${45 * 1.4}px` : '45px',
                rewindAndForward: isDesktop ? `${40 * 1.2}px` : '40px',
                favorite: isDesktop ? `${28 * 1.3}px` : '28px',
                loading: isDesktop ? `${85 * 1.4}px` : '85px',
            };
        },
        landscapeView() {
            return this.device.height < 520;
        },
        xsDevices() {
            return this.device.width < 361;
        },
        showPlayerInline() {
            return this.device.width < 361 || this.device.height < 520;
        },
        favoriteActionText() {
            return this.isFavorite
                ? this.localizations[this.lang].removeFromFavorites
                : this.localizations[this.lang].addToFavorites;
        },
        narrationSpeedActionText() {
            if (this.audiobook.track.speed === 1) {
                return this.localizations[this.lang].normalNarration;
            } else if (this.audiobook.track.speed > 1) {
                return this.localizations[this.lang].fastNarration;
            } else {
                return this.localizations[this.lang].slowNarration;
            }
        },
    },
    methods: {
        ...mapActions(['trackNavigation']),
        ...mapMutations(['setIsPlayBtn', 'togglePaymentLinks']),
        playOrPauseAudio() {
            if (this.isPlayBtn) {
                this.$emit('playtrack');
            } else {
                this.$emit('pausetrack');
            }
            this.togglePlayBtn();
        },
        togglePlayBtn() {
            this.setIsPlayBtn({ isPlayBtn: !this.isPlayBtn });
        },
        rewind() {
            this.$emit('rewindtrack');
            this.trackNavigation({
                action: 'rewind',
                trigger: 'click',
                source: 'audiobook:player',
            });
        },
        forward() {
            this.$emit('forwardtrack');
            this.trackNavigation({
                action: 'forward',
                trigger: 'click',
                source: 'audiobook:player',
            });
        },
        next() {
            this.$emit('nexttrack');
            this.trackNavigation({
                action: 'next',
                trigger: 'click',
                source: 'audiobook:player',
            });
        },
        back() {
            this.$emit('backtrack');
            this.trackNavigation({
                action: 'back',
                trigger: 'click',
                source: 'audiobook:player',
            });
        },
        sendSeekPosition() {
            this.$emit('setseektrack', this.positionSeek);
            this.trackNavigation({
                action: 'rewindOrForward',
                trigger: 'range',
                source: 'audiobook:player',
            });
        },
        pauseSeekPosition() {
            this.$emit('pauseseekposition');
        },
        reachedPreview() {
            this.$emit('pausetrack');
            if (this.isBuyableIssue) {
                this.togglePaymentLinks();
            }
        },
    },
    watch: {
        'track.seek': function (positionSeek) {
            this.positionSeek =
                isNaN(positionSeek) || typeof positionSeek !== 'number' ? this.positionSeek : positionSeek;

            if (
                this.isPreviewMode &&
                this.isLatestPreviewTrack &&
                this.track.durationInSecondsPreview < this.positionSeek
            ) {
                this.reachedPreview();
            }
        },
    },
    filters: {
        ...{ secondToMMSS },
    },
};
</script>
<style lang="scss" scoped>
.AudiobookPlayer {
    color: var(--theme-text-color);

    .flatButton.rewind:active,
    .flatButton.forward:active {
        transition: transform 50ms ease;
    }
    .flatButton.rewind:active {
        transform: rotate(-10deg);
    }
    .flatButton.forward:active {
        transform: rotate(10deg);
    }

    .timing {
        @media screen and (min-width: 980px) {
            font-size: 1.2rem;
        }
    }
}
</style>
<style lang="scss">
@import '_shared_/sass/components/_input-range-variables.scss';
// change the size of `range` in audiobooks only.
$track-height: 8px;
$thumb-radius: 30px;
$thumb-height: 26px;
$thumb-width: 26px;

.AudiobookPlayer {
    button[data-action='playOrPause'] svg {
        width: 85px;
        height: 85px;
        @media screen and (min-width: 980px) {
            width: calc(85px * 1.4);
            height: calc(85px * 1.4);
        }
    }
    button[data-action='back'] svg,
    button[data-action='next'] svg {
        width: 45px;
        height: 45px;
        @media screen and (min-width: 980px) {
            width: calc(45px * 1.4);
            height: calc(45px * 1.4);
        }
    }
    button[data-action='rewind'] svg,
    button[data-action='forward'] svg {
        width: 45px;
        height: 45px;
        @media screen and (min-width: 980px) {
            width: calc(45px * 1.4);
            height: calc(45px * 1.4);
        }
    }
    button[data-action='favorite'] svg {
        width: 30px;
        height: 30px;
        @media screen and (min-width: 980px) {
            width: calc(30px * 1.4);
            height: calc(30px * 1.4);
        }
    }
}

//range input styles. If this is scope, whe can't use inside a portal.
@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
    box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color, 0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track {
    cursor: pointer;
    height: $track-height;
    transition: all 0.2s ease;
    width: $track-width;
}

@mixin thumb($adjustment: 0) {
    @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);
    background: $thumb-color;
    border: $thumb-border-width solid $thumb-border-color;
    border-radius: $thumb-radius;
    cursor: grab;
    height: $thumb-height + $adjustment;
    width: $thumb-width + $adjustment;
}

@mixin disabled {
    cursor: not-allowed;
}

[type='range'] {
    -webkit-appearance: none;
    background: transparent;
    margin: 0;
    height: $thumb-height;
    width: $track-width;

    &::-moz-focus-outer {
        border: 0;
    }

    &:focus {
        outline: 0;

        &::-webkit-slider-runnable-track {
            background: lighten($track-color, $contrast);
        }

        &::-ms-fill-lower {
            background: $track-color;
        }

        &::-ms-fill-upper {
            background: lighten($track-color, $contrast);
        }
    }

    &::-webkit-slider-runnable-track {
        @include track;
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
    }

    &::-webkit-slider-thumb {
        @include thumb;
        -webkit-appearance: none;
        margin-top: -(math.div($thumb-height, 2) + math.div($track-height, 2) - $track-border-width);
    }

    &::-moz-range-track {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        @include track;
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
        height: math.div($track-height, 2);
    }

    &::-moz-range-thumb {
        @include thumb(-4);
    }

    &::-ms-track {
        @include track;
        background: transparent;
        border-color: transparent;
        border-width: math.div($thumb-height, 2);
        color: transparent;
    }

    &::-ms-fill-lower {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $ie-bottom-track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: ($track-radius * 2);
    }

    &::-ms-fill-upper {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: ($track-radius * 2);
    }

    &::-ms-thumb {
        @include thumb(-4);
        margin-top: math.div($track-height, 4);
    }

    &:disabled {
        &::-webkit-slider-thumb {
            @include disabled;
        }

        &::-moz-range-thumb {
            @include disabled;
        }

        &::-ms-thumb {
            @include disabled;
        }

        &::-webkit-slider-runnable-track {
            @include disabled;
        }

        &::-ms-fill-lower {
            @include disabled;
        }

        &::-ms-fill-upper {
            @include disabled;
        }
    }

    &::-ms-thumb {
        transition: transform 100ms ease;
    }
    &::-moz-range-thumb {
        transition: transform 100ms ease;
    }
    &::-webkit-slider-thumb {
        transition: transform 100ms ease;
    }
    &:active::-ms-thumb {
        cursor: grabbing;
        transform: scale(1.2);
    }
    &:active::-moz-range-thumb {
        cursor: grabbing;
        transform: scale(1.2);
    }
    &:active::-webkit-slider-thumb {
        cursor: grabbing;
        transform: scale(1.2);
    }
}
</style>
