<template>
    <div class="AudiobookPlaylist" :class="{ 'pt-4 backgroundInMobile': onlyMobileVisible }">
        <div class="container-fluid">
            <div class="row" v-if="onlyMobileVisible">
                <div class="col-12">
                    <h4 class="title">{{ localizations[lang].tableOfContents }}</h4>
                </div>
            </div>
            <ul class="row p-0">
                <li
                    class="col-12 py-4"
                    v-for="(track, index) in playlist"
                    :key="index"
                    :class="[index === currentTrackIndex ? 'isSelect' : '', !track.file ? 'disabled' : '']"
                    @click="track.file ? playChapter(index) : ''"
                    :data-cy="`track-${track.chapter}`"
                >
                    <div class="d-flex align-items-center pb-2">
                        <div class="d-flex align-items-center">
                            <span>{{ track.chapter }}</span>
                            <svg-icon
                                class="ml-2"
                                v-if="index === currentTrackIndex"
                                icon="baseline-volume_up-24px"
                                width="16px"
                                height="16px"
                                :data-cy="`listenTrack-${track.chapter}`"
                            ></svg-icon>
                        </div>
                        <div class="ml-auto d-flex align-items-center">
                            <span
                                v-if="isPreviewMode && track.durationInSecondsPreview"
                                class="badge badge-inline mr-2"
                            >
                                {{ localizations[lang].preview }}
                                <b
                                    class="font-monospace ml-2"
                                    :data-cy="`previewPlaylistTrackDuration-${track.chapter}`"
                                >
                                    {{ track.durationInSecondsPreview | secondToMMSS }}
                                </b>
                            </span>
                            <span
                                v-if="showTrackDuration(track)"
                                class="font-monospace"
                                :data-cy="`playlistTrackDuration-${track.chapter}`"
                            >
                                {{ track.duration }}
                            </span>
                        </div>
                    </div>
                    <h4 class="mb-0">{{ track.title }}</h4>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import secondToMMSS from '_shared_/js/utils/secondToMMSS.js';
export default {
    props: {
        playlist: Array,
        currentTrackIndex: Number,
        isPlaying: Boolean,
    },
    computed: {
        ...mapState(['localizations', 'lang']),
        ...mapGetters(['onlyMobileVisible', 'isPreviewMode']),
    },
    methods: {
        ...mapActions(['trackNavigation']),
        playChapter(index) {
            if (index === this.currentTrackIndex) return;
            this.$emit('playtrack', index);
            this.trackNavigation({
                action: 'select chapter',
                trigger: 'click',
                page: '',
                source: 'audiobook:playlist',
            });
        },
        showTrackDuration(track) {
            if (!this.isPreviewMode) {
                return true;
            }

            return track.durationInSecondsPreview != track.durationInSeconds;
        },
    },
    filters: {
        ...{ secondToMMSS },
    },
};
</script>
<style lang="scss" scoped>
.AudiobookPlaylist {
    // fix overlapping playlist in chrome mobile browser
    padding-bottom: 4rem;

    &.backgroundInMobile {
        background: rgba(255, 255, 255, 0.05);
    }
    .title {
        font-size: 1.25rem;
        color: var(--theme-text-color);
    }
    ul,
    li {
        list-style: none;
        cursor: pointer;
        color: var(--theme-text-color);
    }
    li:hover {
        background: rgba(#707070, 0.09);
    }
    li:active,
    li.isSelect {
        background: rgba(#707070, 0.12);
        outline: 0;
    }
    li.disabled {
        opacity: 0.2;
    }
    li:after {
        content: '';
        position: absolute;
        top: 0;
        height: 1px;
        width: calc(100% - 30px);
        background-color: var(--theme-border-color);
    }
    li:first-child:after {
        background-color: transparent;
    }
}
</style>
